<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="container cartheight">
      <nav></nav>
      <div class="row justify-content-center align-items-center">
        <!-- <div class="col-sm-12 ">
          <div class="cart_title d-flex align-items-center p-2">
           <h3 class="font-weight-normal">購物車( {{ carts.length }}件 )</h3></div>
        </div> -->
        <div class="col-sm-12 align-self-center">
          <div id="cartContent" v-if="carts.length > 0">
            <!-- 顯示購物車內容 -->
            <div id="cartTable" class="border p-3">
              <div class="row d-flex justify-content-end pl-3 pr-3">
                <div
                  class="col-3 d-flex justify-content-end align-items-center"
                  style="height: 40px;"
                >
                  <button class="btn btn-sm" @click="removeAllCartItem()">
                    <i class="far fa-trash-alt mr-1"></i>
                    <span>清空全部</span>
                  </button>
                </div>
              </div>
              <div
                class="row d-flex justify-content-between pl-3 pr-3 mb-3"
                v-for="(item) in carts"
                :key="item.product.id + 1"
              >
                <div class="cart_img">
                  <img :src="item.product.imageUrl[0]">
                </div>
                <div
                  class="col-3 d-flex justify-content-start align-items-center"
                  style="height: 60px;"
                >{{ item.product.title }}</div>
                <!-- quantity -->
                <div
                  class="col-xs-4 d-flex justify-content-center align-items-center text-center"
                  style="height: 60px;"
                >
                  <button type="button" class="btn-quantity-control  btn text-center">
                    <i class="fa fa-minus"></i>
                  </button>
                  <div class="numbox input-group input-group-sm m-0">
                    <input
                      type="number"
                      class="form-control text-center"
                      :value="item.quantity"
                      @input="updateQuantity(item.product.id, $event.target.value)"
                    />
                  </div>
                  <button type="button" class="btn-quantity-control  btn">
                    <i class="fa fa-plus"></i>
                  </button>
                </div>
                <!-- unit -->
                <div
                  class="col-2 d-flex justify-content-center align-items-center"
                  style="height: 60px;"
                >{{ item.quantity }} {{ item.product.unit }}</div>
                <div
                  class="col-2 d-flex justify-content-center align-items-center"
                  style="height: 60px;"
                >NT. {{ item.product.price * item.quantity }}</div>
                <div
                  class="col-1 d-flex justify-content-center align-items-center"
                  style="height: 60px;"
                >
                  <a href="#" @click.prevent="removeCartItem(item.product.id)">
                    <i class="fas fa-times text-danger bolder"></i>
                  </a>
                </div>
              </div>
            </div>
            <!-- 顯示金額 -->
            <div id="sumTable" class="d-flex justify-content-end pl-3 pr-3">
              <div class="col-md-8 d-flex justify-content-end align-items-center">
                <div class="row d-flex justify-content-end">
                  <div class="col-12 d-flex justify-content-end align-items-start pb-2">
                    <table class="table text-right">
                      <tbody>
                        <tr>
                          <td scope="col"></td>
                          <td scope="col">總計</td>
                          <td scope="col">
                            <span v-if="cartTotal >= 999">NT. {{ cartTotal }}</span>
                            <span v-else>NT. {{ cartTotal + 80 }}</span>
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
            <!-- button -->
            <div class="fnbar row d-flex justify-content-between align-items-center mt-3">
              <div class="col-xs-4 d-flex justify-content-start align-items-center pl-3">
                <router-link to="/products" class="btn btn-outline-primary">繼續購物</router-link>
              </div>
              <div class="col-xs-4 d-flex justify-content-end align-items-center pr-3">
                <router-link
                  to="/finish"
                  class="btn btn-primary"
                  :class="{ disabled: cartTotal <= 0 }"
                >訂單結帳</router-link>
              </div>
            </div>
          </div>

          <div
            id="noCartContent"
            class="row d-flex justify-content-center align-items-start mt-3 my-3"
            v-else
          >
            <div class="col-12 text-center justify-content-center align-items-center  p-3 mt-3">
              <p class="mb-3">您的購物車目前是空的喔</p>
                <router-link to="/products" class="btn btn-secondary"> 繼續購物</router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Cart',
  data () {
    return {
      isLoading: false,
      fullPage: true,
      products: {},
      coupon: {},
      coupon_code: '',
      carts: {},
      cartTotal: 0,
      quantityTatol: 0,
      orignalTotal: 0,
      uuid: process.env.VUE_APP_UUID
    }
  },
  created () {
    this.getCart()
  },
  methods: {
    getCart () {
      // 獲取購物車資訊
      this.isLoading = true
      const url = `${process.env.VUE_APP_APIPATH}/api/${this.uuid}/ec/shopping`
      this.$http
        .get(url)
        .then((res) => {
          this.carts = res.data.data
          this.calTotal()
          this.isLoading = false
        })
        .catch((err) => {
          this.isLoading = false
          this.$bus.$emit(
            'message:push',
            `資料讀取失敗，請稍後再試
            ${err}`,
            'danger'
          )
        })
    },
    // 計算商品總件數及總價
    calTotal () {
      let sumTotal = 0
      let sumQuantity = 0
      this.carts.forEach((item) => {
        sumTotal += item.product.price * item.quantity
        sumQuantity += item.quantity
      })
      this.cartTotal = sumTotal
      this.quantityTatol = sumQuantity
    },
    updateQuantity (id, quantity = 1) {
      this.isLoading = true
      const url = `${process.env.VUE_APP_APIPATH}/api/${this.uuid}/ec/shopping`
      const data = {
        product: id,
        quantity: quantity
      }
      this.$http.patch(url, data).then(() => {
        this.getCart()
        this.isLoading = false
      })
    },
    removeCartItem (id) {
      this.isLoading = true
      const url = `${process.env.VUE_APP_APIPATH}/api/${this.uuid}/ec/shopping/${id}`
      this.$http
        .delete(url)
        .then(() => {
          this.$bus.$emit('update-total')
          this.getCart()
          this.isLoading = false
          this.$bus.$emit('message:push', '商品已刪除', 'success')
        })
        .catch((err) => {
          this.isLoading = false
          this.$bus.$emit(
            'message:push',
            `商品刪除失敗
          ${err}`,
            'danger'
          )
        })
    },
    removeAllCartItem () {
      this.isLoading = true
      const url = `${process.env.VUE_APP_APIPATH}/api/${this.uuid}/ec/shopping/all/product`
      this.$http
        .delete(url)
        .then(() => {
          this.$bus.$emit('update-total')
          this.getCart()
          this.cartTotal = 0
          this.isLoading = false
          this.$bus.$emit(
            'message:push',
            '商品已全部刪除，將返回產品頁面',
            'success'
          )
          this.$router.push('/products')
        })
        .catch((err) => {
          this.isLoading = false
          this.$bus.$emit(
            'message:push',
            `商品刪除失敗
          ${err}`,
            'danger'
          )
        })
    }
  }
}
</script>
